import TicketRemark from '@/componets/TicketRemark.vue'
export default {
  name: 'orderDetail',
  props: {
    detail: Object
  },
  components: {
    TicketRemark
  },
  data () {
    return {
		shopName: { title: '下单店铺', value: this.detail.shop_name },
		tradeNo: { title: '订单编号', value: this.detail.ord_id }
    }
  },
  computed:{
	  orderLog () {
		  let orderLogs = []
		  this.detail.order_log.forEach((item) => {
			  orderLogs.push({
				  title: item.type,
				  value: item.ctime
			  })
		  })
		  return orderLogs
	  }
  }
}
